  background-color: #e5f1fb;
}
.layui-layer-win10 .layui-layer-btn .layui-layer-btn0 {
  border-color: #0078d7;
}

/**
 * layer 
 */

/* prompt */
.layui-layer-prompt .layui-layer-input {
  display: block;
  width: 260px;
  height: 36px;
  margin: 0 auto;
  line-height: 30px;
  padding-left: 10px;
  border: 1px solid #e6e6e6;
  color: #333;
}
.layui-layer-prompt textarea.layui-layer-input {
  width: 300px;
  height: 100px;
  line-height: 20px;
  padding: 6px 10px;
}
.layui-layer-prompt .layui-layer-content {
  padding: 16px;
}
.layui-layer-prompt .layui-layer-btn {
  padding-top: 0;
}

/* tab */
.layui-layer-tab {
  box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.4);
}
.layui-layer-tab .layui-layer-title {
  padding-left: 0;
  overflow: visible;
}
.layui-layer-tab .layui-layer-title span {
  position: relative;
  display: inline-block;
  vertical-align: top;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  min-width: 80px;
  max-width: 300px;
  padding: 0 16px;
  text-align: center;
  cursor: default;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}
.layui-layer-tab .layui-layer-title span.layui-this {
  height: 51px;
  border-left-color: #eee;
  border-right-color: #eee;
  background-color: #fff;
  z-index: 10;
}
.layui-layer-tab .layui-layer-title span:first-child {
  border-left-color: transparent;
}
.layui-layer-tabmain {
  line-height: 24px;
  clear: both;
}
.layui-layer-tabmain .layui-layer-tabli {
  display: none;
}
.layui-layer-tabmain .layui-layer-tabli.layui-this {
  display: block;
}

/* photos */
.layui-layer-photos {
  background: none;
  box-shadow: none;
}
.layui-layer-photos .layui-layer-content {
  overflow: visible;
  text-align: center;
}
.layui-layer-photos .layer-layer-photos-main img {
  position: relative;
  width: 100%;
  display: inline-block;
  vertical-align: top;
}
.layui-layer-photos-prev,
.layui-layer-photos-next {
  position: fixed;
  top: 50%;
  width: 52px;
  height: 52px;
  line-height: 52px;
  margin-top: -26px;
  cursor: pointer;
  font-size: 52px;
  color: #717171;
}
.layui-layer-photos-prev {
  left: 32px;
}
.layui-layer-photos-next {
  right: 32px;
}
.layui-layer-photos-prev:hover,
.layui-layer-photos-next:hover {
  color: #959595;
}

.layui-layer-photos-toolbar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 52px;
  line-height: 52px;
  background-color: rgba(0, 0, 0, 0.32);
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 0;
}
.layui-layer-photos-toolbar > * {
  display: inline-block;
  vertical-align: top;
  padding: 0 16px;
  font-size: 12px;
  color: #fff;
}
.layui-layer-photos-toolbar * {
  font-size: 12px;
}
.layui-layer-photos-header {
  top: 0;
  bottom: auto;
}
.layui-layer-photos-header > span {
  cursor: pointer;
}
.layui-layer-photos-header > span:hover {
  background-color: rgba(51, 51, 51, 0.32);
}
.layui-layer-photos-header .layui-icon {
  font-size: 18px;
}
.layui-layer-photos-footer > h3 {
  max-width: 65%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.layui-layer-photos-footer a:hover {
  text-decoration: underline;
}
.layui-layer-photos-footer em {
  font-style: normal;
}

/*  */
@-webkit-keyframes layer-bounceOut {
  100% {
    opacity: 0;
    -webkit-transform: scale(0.7);
    transform: scale(0.7);
  }
  30% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
  }
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes layer-bounceOut {
  100% {
    opacity: 0;
    -webkit-transform: scale(0.7);
    -ms-transform: scale(0.7);
    transform: scale(0.7);
  }
  30% {
    -webkit-transform: scale(1.05);
    -ms-transform: scale(1.05);
    transform: scale(1.05);
  }
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}
.layer-anim-close {
  -webkit-animation-name: layer-bounceOut;
  animation-name: layer-bounceOut;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
}
