/**
 * code
 */

html #layuicss-skincodecss {
  display: none;
  position: absolute;
  width: 1989px;
}

/*   */
.layui-code-wrap {
  font-size: 13px;
  font-family: 'Courier New', Consolas, 'Lucida Console', monospace;
}

/*  */
.layui-code-view {
  display: block;
  position: relative;
  padding: 0 !important;
  border: 1px solid #eee;
  border-left-width: 6px;
  background-color: #fff;
  color: #333;
}
.layui-code-view pre {
  margin: 0 !important;
}

.layui-code-header {
  position: relative;
  z-index: 3;
  padding: 0 11px;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  background-color: #fafafa;
  font-size: 12px;
}
.layui-code-header > .layui-code-header-about {
  position: absolute;
  right: 11px;
  top: 0;
  color: #b7b7b7;
}
.layui-code-header-about > a {
  padding-left: 10px;
}

.layui-code-wrap {
  position: relative;
  display: block;
  z-index: 1;
  margin: 0 !important;
  padding: 11px 0 !important;
  overflow-x: hidden;
  overflow-y: auto;
}
.layui-code-line {
  position: relative;
  line-height: 19px;
  margin: 0 !important;
}
.layui-code-line-number {
  position: absolute;
  left: 0;
  top: 0;
  padding: 0 8px;
  min-width: 45px;
  height: 100%;
  text-align: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  overflow: hidden;
}
.layui-code-line-content {
  padding: 0 11px;
  word-wrap: break-word;
  white-space: pre-wrap;
}

.layui-code-ln-mode > .layui-code-wrap > .layui-code-line {
  padding-left: 45px;
}
.layui-code-ln-side {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  width: 45px;
  border-right: 1px solid #eee;
  border-color: rgb(126 122 122 / 15%);
  background-color: #fafafa;
  pointer-events: none;
}

/*   */
.layui-code-nowrap > .layui-code-wrap {
  overflow: auto;
}
.layui-code-nowrap
  > .layui-code-wrap
  > .layui-code-line
  > .layui-code-line-content {
  white-space: pre;
  word-wrap: normal;
}
.layui-code-nowrap > .layui-code-ln-side {
  border-right-width: 0 !important;
  background: none !important;
}

.layui-code-fixbar {
  position: absolute;
  top: 8px;
  right: 11px;
  padding-right: 45px;
  z-index: 5;
}
.layui-code-fixbar > span {
  position: absolute;
  right: 0;
  top: 0;
  padding: 0 8px;
  color: #777;
  transition: all 0.3s;
}
.layui-code-fixbar > span:hover {
  color: #16b777;
}
.layui-code-copy {
  display: none;
  cursor: pointer;
}
.layui-code-preview > .layui-code-view > .layui-code-fixbar .layui-code-copy {
  display: none !important;
}
.layui-code-view:hover > .layui-code-fixbar .layui-code-copy {
  display: block;
}
.layui-code-view:hover > .layui-code-fixbar .layui-code-lang-marker {
  display: none;
}

/*  */
.layui-code-theme-dark,
.layui-code-theme-dark > .layui-code-header {
  border-color: rgb(126 122 122 / 15%);
  background-color: #1f1f1f;
}
.layui-code-theme-dark {
  border-width: 1px;
  color: #ccc;
}
.layui-code-theme-dark > .layui-code-ln-side {
  border-right-color: #2a2a2a;
  background: none;
  color: #6e7681;
}

/*  */
.layui-code textarea {
  display: none;
}
.layui-code-preview > .layui-code,
.layui-code-preview > .layui-code-view {
  margin: 0;
}
.layui-code-preview > .layui-tab {
  position: relative;
  z-index: 1;
  margin-bottom: 0;
}
.layui-code-preview .layui-code-item {
  display: none;
  border-top-width: 0;
}
.layui-code-item-preview {
  position: relative;
  padding: 16px;
}
.layui-code-item-preview > iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

/*   */
.layui-code-tools {
  position: absolute;
  right: 11px;
  top: 8px;
  line-height: normal;
}
.layui-code-tools > i {
  display: inline-block;
  margin-left: 6px;
  padding: 3px;
  cursor: pointer;
}
.layui-code-tools > i.layui-icon-file-b {
  color: #999;
}
.layui-code-tools > i:hover {
  color: #16b777;
}

/*   */
.layui-code-full {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1111111;
  width: 100%;
  height: 100%;
  background-color: #fff;
}
.layui-code-full .layui-code-item {
  width: 100% !important;
  border-width: 0 !important;
}
.layui-code-full .layui-code-item,
.layui-code-full .layui-code-view,
.layui-code-full .layui-code-wrap {
  height: calc(100vh - 51px) !important;
  box-sizing: border-box;
}
.layui-code-full .layui-code-item-preview {
  overflow: auto;
}

/*  */
.layui-code-view.layui-code-hl {
  line-height: 20px !important;
  border-left-width: 1px;
}
.layui-code-view.layui-code-hl > .layui-code-ln-side {
  background-color: transparent;
}
.layui-code-theme-dark.layui-code-hl,
.layui-code-theme-dark.layui-code-hl > .layui-code-ln-side {
  border-color: rgb(126 122 122 / 15%);
}

/**/
.layui-code-line-highlighted {
  background-color: rgba(142, 150, 170, 0.14);
}
.layui-code-line-diff-add {
  background-color: rgba(16, 185, 129, 0.14);
}
.layui-code-line-diff-remove {
  background-color: rgba(244, 63, 94, 0.14);
}
.layui-code-line-diff-add:before {
  position: absolute;
  content: '+';
  color: #18794e;
}
.layui-code-line-diff-remove:before {
  position: absolute;
  content: '-';
  color: #b8272c;
}
.layui-code-has-focused-lines .layui-code-line:not(.layui-code-line-has-focus) {
  filter: blur(0.095rem);
  opacity: 0.7;
  -webkit-transition:
    filter 0.35s,
    opacity 0.35s;
  transition:
    filter 0.35s,
    opacity 0.35s;
}
.layui-code-has-focused-lines:hover
  .layui-code-line:not(.layui-code-line-has-focus) {
  filter: blur();
  opacity: 1;
}
